import React from 'react';
import './index.css';
const ViewType = ( props ) => {

	const ViewTypes = props.viewTypes.map( ( type, index ) => {
		return ( <label key={`viewType${index}`}>
			<input type="radio" value={type} checked={props.viewType == type} onChange={props.onchange} /> {type.charAt( 0 ).toUpperCase() + type.slice( 1 )}
		</label> );
		//return <ViewSource key={`form-${form.id}`} formId={form.id} label={form.label} />
	} )
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	let upsellLink = '';
	if ( view_config.prefix === 'nf' ) {
		upsellLink = 'https://nfviews.com/pricing/?utm_source=view-edit-page&utm_medium=nfviews-advance-settings&utm_campaign=nf-views-lite-version'
	} else if ( view_config.prefix === 'cf7' ) {
		upsellLink = 'https://cf7views.com/pricing/?utm_source=view-edit-page&utm_medium=cf7views-advance-settings&utm_campaign=cf7-views-lite-version'
	} else {
		upsellLink = 'https://formviewswp.com/pricing/?utm_source=view-edit-page&utm_medium=wpformsviews-advance-settings&utm_campaign=wpforms-views-lite-version'
	}
	return (
		<div className="viewSourceCont pure-form viewSectionContainer view-type">
			<legend>Select View Type</legend>
			<fieldset>
				{ViewTypes}
				<label>
					<input type="radio" disabled="disabled" value="" /> List
				</label>
				<label>
					<input type="radio" disabled="disabled" value="" /> DataTable
				</label>

				<div className="views-pro-upsell">
					<i className="fas fa-info-circle"></i>Want to use List or DataTable View Type? <a className="pro-upsell-link" href={upsellLink} target="_blank">Check out Pro Version!</a>.
				</div>
			</fieldset>
		</div>


	);
}

export default ViewType;