import React from 'react';

const MultipleEntriesSettings = ( props ) => {
	const viewSettingsProps = props.viewSettingsProps;
	let wpformsOptions = '';
	if ( props.viewPrefix === 'wpforms' ) {
		wpformsOptions = <div><div className="pure-form advance-settings-row">

			<label ><a className="pro-upsell-link" href={props.upsellLink} target="_blank"><input disabled="disabled" name="starred" type="checkbox" /><span>Display only Starred Entries</span><span className="pro-upsell-text">PRO</span>
				<i className="fas fa-external-link-alt"></i></a></label>


		</div>
			<div className="pure-form advance-settings-row">

				<label ><a className="pro-upsell-link" href={props.upsellLink} target="_blank"><input disabled="disabled" name="viewed" type="checkbox" /><span>Display only Viewed Entries</span><span className="pro-upsell-text">PRO</span>
					<i className="fas fa-external-link-alt"></i></a></label>

			</div></div>
	}
	return ( <div>
		<div className="pure-form advance-settings-row">
			<span>Submissions Per Page</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
			<input name="perPage" type="text" value={viewSettingsProps.viewSettings.multipleentries.perPage} onChange={( e ) => viewSettingsProps.saveViewSetting( e, 'multipleentries' )} />
		</div>

		<div className="pure-form advance-settings-row">

			<label ><a className="pro-upsell-link" href={props.upsellLink} target="_blank"><input disabled="disabled" name="loggedin" type="checkbox" /><span>Display Only Logged in User Entries</span><span className="pro-upsell-text">PRO</span>
				<i className="fas fa-external-link-alt"></i>
			</a></label>

		</div>
		<div className="pure-form advance-settings-row">

			<label ><input disabled="disabled" name="loggedin" type="checkbox" /><a className="pro-upsell-link" href={props.upsellLink} target="_blank"><span>Hide View Data until search is performed</span><span className="pro-upsell-text">PRO</span>
				<i className="fas fa-external-link-alt"></i>
			</a></label>

		</div>
		<div className="pure-form advance-settings-row">

			<label ><a className="pro-upsell-link" href={props.upsellLink} target="_blank"><input disabled="disabled" name="approvedSubmissions" type="checkbox" /><span>Display only Approved Submissions</span><span className="pro-upsell-text">PRO</span>
				<i className="fas fa-external-link-alt"></i></a></label>

		</div>
		{wpformsOptions}
	</div>

	)

}

export default MultipleEntriesSettings;