import React from 'react';
import './index.css';

const FieldsModal = ( props ) => {
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	const modalProps = props.modalProps;
	let upsellLink = '';
	let fields = Object.keys( modalProps.formFields ).map( ( fieldId, index ) => {
		let fieldInfo = '';
		if ( modalProps.activeSection != 'beforeloop' && modalProps.activeSection != 'afterloop' ) {
			fieldInfo = `( ID:${fieldId} )`;
		}
		return <li key={`add${fieldId}`} onClick={( event ) => modalProps.addField( event, fieldId )}>
			{modalProps.formFields[ fieldId ].label}
			{fieldInfo}
		</li>
	} );

	if ( view_config.prefix === 'nf' ) {
		upsellLink ='https://nfviews.com/pricing/?utm_source=view-edit-page&utm_medium=nfviews-field-popup&utm_campaign=nf-views-lite-version'
	} else if ( view_config.prefix === 'cf7' ) {
		upsellLink ='https://cf7views.com/pricing/?utm_source=view-edit-page&utm_medium=cf7views-field-popup&utm_campaign=cf7-views-lite-version'
	} else {
		upsellLink ='https://formviewswp.com/pricing/?utm_source=view-edit-page&utm_medium=wpformsviews-field-popup&utm_campaign=wpforms-views-lite-version'
	}

	if ( modalProps.activeSection === 'beforeloop' || modalProps.activeSection === 'afterloop' ) {
		fields.push( <li key="searchWidgetField"><a target="_blank" href={upsellLink}>Search<span className="pro-upsell-text">PRO</span></a></li> );
		fields.push( <li key="customHTMLWidgetField" ><a target="_blank" href={upsellLink}>Custom HTML<span className="pro-upsell-text">PRO</span></a></li> );
		fields.push( <li key="datatableWidgetField" ><a target="_blank" href={upsellLink}>DataTable Buttons<span className="pro-upsell-text">PRO</span></a></li> );
	}

	return (
		<ul className="add-fields-list">
			{fields}
		</ul>
	)

}

export default FieldsModal;